<template>
  <v-card
    height="100%"
  >
    <div
      id="nodocentes-cargos-habilitados-vs-liquidados-VICE"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Vicerrectorado: Cargos habilitados y liquidados por Categoría.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('nodocentes-cargos-habilitados-vs-liquidados-VICE')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as No Docentes.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Categoría
                </th>
                <th class="text-left">
                  Cargos habilitados
                </th>
                <th class="text-left">
                  Cargos liquidados
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in categoriaData"
                :key="index"
              >
                <td>{{ parseCategoria(index) }}</td>
                <td>{{ item.habilitados }}</td>
                <td>{{ item.liquidados }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['nodocentesCargos', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 150,
          right: 20,
          top: 20,
          bottom: 20
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: this.colors,
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        legend: {
          position: 'top',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        isStacked: true,
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Categoria', label: 'Cargos', type: 'string' },
          { id: 'CategoriaHabilitados', label: 'Cargos Habilitados', type: 'number' },
          { id: 'CategoriaLiquidados', label: 'Cargos Liquidados', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'Cat 1 CCT 366/06' },
              { v: this.categoriaData.cat1.habilitados, f: this.categoriaData.cat1.habilitados },
              { v: this.categoriaData.cat1.liquidados, f: this.categoriaData.cat1.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 2 CCT 366/06' },
              { v: this.categoriaData.cat2.habilitados, f: this.categoriaData.cat2.habilitados },
              { v: this.categoriaData.cat2.liquidados, f: this.categoriaData.cat2.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 3 CCT 366/06' },
              { v: this.categoriaData.cat3.habilitados, f: this.categoriaData.cat3.habilitados },
              { v: this.categoriaData.cat3.liquidados, f: this.categoriaData.cat3.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 4 CCT 366/06' },
              { v: this.categoriaData.cat4.habilitados, f: this.categoriaData.cat4.habilitados },
              { v: this.categoriaData.cat4.liquidados, f: this.categoriaData.cat4.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 5 CCT 366/06' },
              { v: this.categoriaData.cat5.habilitados, f: this.categoriaData.cat5.habilitados },
              { v: this.categoriaData.cat5.liquidados, f: this.categoriaData.cat5.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 6 CCT 366/06' },
              { v: this.categoriaData.cat6.habilitados, f: this.categoriaData.cat6.habilitados },
              { v: this.categoriaData.cat6.liquidados, f: this.categoriaData.cat6.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 7 CCT 366/06' },
              { v: this.categoriaData.cat7.habilitados, f: this.categoriaData.cat7.habilitados },
              { v: this.categoriaData.cat7.liquidados, f: this.categoriaData.cat7.liquidados }
            ]
          }
        ]
      }
    },
    categoriaData () {
      // "Cat 1 CCT 366/06"
      // "Cat 2 CCT 366/06"
      // "Cat 3 CCT 366/06"
      // "Cat 4 CCT 366/06"
      // "Cat 5 CCT 366/06"
      // "Cat 6 CCT 366/06"
      // "Cat 7 CCT 366/06"
      return this.nodocentesCargos
        .filter(nodocente => nodocente.unidad_academica === 'VICE')
        .reduce(
          (acc, cur) => {
            const subr1 = new RegExp('SubR: Cat 1', 'gm')
            const subr2 = new RegExp('SubR: Cat 2', 'gm')
            const subr3 = new RegExp('SubR: Cat 3', 'gm')
            const subr4 = new RegExp('SubR: Cat 4', 'gm')
            const subr5 = new RegExp('SubR: Cat 5', 'gm')
            const subr6 = new RegExp('SubR: Cat 6', 'gm')
            const cate1 = cur.descripcion_categoria === 'Cat 1 CCT 366/06'
            const cate2 = cur.descripcion_categoria === 'Cat 2 CCT 366/06'
            const cate3 = cur.descripcion_categoria === 'Cat 3 CCT 366/06'
            const cate4 = cur.descripcion_categoria === 'Cat 4 CCT 366/06'
            const cate5 = cur.descripcion_categoria === 'Cat 5 CCT 366/06'
            const cate6 = cur.descripcion_categoria === 'Cat 6 CCT 366/06'
            const cate7 = cur.descripcion_categoria === 'Cat 7 CCT 366/06'
            return subr1.test(cur.subrogancia)
              ? { ...acc, cat1: { habilitados: acc.cat1.habilitados, liquidados: acc.cat1.liquidados + 1 } }
              : subr2.test(cur.subrogancia)
                ? { ...acc, cat2: { habilitados: acc.cat2.habilitados, liquidados: acc.cat2.liquidados + 1 } }
                : subr3.test(cur.subrogancia)
                  ? { ...acc, cat3: { habilitados: acc.cat3.habilitados, liquidados: acc.cat3.liquidados + 1 } }
                  : subr4.test(cur.subrogancia)
                    ? { ...acc, cat4: { habilitados: acc.cat4.habilitados, liquidados: acc.cat4.liquidados + 1 } }
                    : subr5.test(cur.subrogancia)
                      ? { ...acc, cat5: { habilitados: acc.cat5.habilitados, liquidados: acc.cat5.liquidados + 1 } }
                      : subr6.test(cur.subrogancia)
                        ? { ...acc, cat6: { habilitados: acc.cat6.habilitados, liquidados: acc.cat6.liquidados + 1 } }
                        : cate1
                          ? { ...acc, cat1: { liquidados: acc.cat1.liquidados, habilitados: acc.cat1.habilitados + 1 } }
                          : cate2
                            ? { ...acc, cat2: { liquidados: acc.cat2.liquidados, habilitados: acc.cat2.habilitados + 1 } }
                            : cate3
                              ? { ...acc, cat3: { liquidados: acc.cat3.liquidados, habilitados: acc.cat3.habilitados + 1 } }
                              : cate4
                                ? { ...acc, cat4: { liquidados: acc.cat4.liquidados, habilitados: acc.cat4.habilitados + 1 } }
                                : cate5
                                  ? { ...acc, cat5: { liquidados: acc.cat5.liquidados, habilitados: acc.cat5.habilitados + 1 } }
                                  : cate6
                                    ? { ...acc, cat6: { liquidados: acc.cat6.liquidados, habilitados: acc.cat6.habilitados + 1 } }
                                    : cate7
                                      ? { ...acc, cat7: { liquidados: acc.cat7.liquidados, habilitados: acc.cat7.habilitados + 1 } }
                                      : acc
          },
          {
            cat1: { habilitados: 0, liquidados: 0 },
            cat2: { habilitados: 0, liquidados: 0 },
            cat3: { habilitados: 0, liquidados: 0 },
            cat4: { habilitados: 0, liquidados: 0 },
            cat5: { habilitados: 0, liquidados: 0 },
            cat6: { habilitados: 0, liquidados: 0 },
            cat7: { habilitados: 0, liquidados: 0 }
          }
        )
    }
  },
  methods: {
    parseCategoria (v) {
      return v === 'cat1'
        ? 'Cat 1 CCT 366/06'
        : v === 'cat2'
          ? 'Cat 2 CCT 366/06'
          : v === 'cat3'
            ? 'Cat 3 CCT 366/06'
            : v === 'cat4'
              ? 'Cat 4 CCT 366/06'
              : v === 'cat5'
                ? 'Cat 5 CCT 366/06'
                : v === 'cat6'
                  ? 'Cat 6 CCT 366/06'
                  : v === 'cat7'
                    ? 'Cat 7 CCT 366/06'
                    : v
    }
  }
}
</script>
